.loadingwrapper{
    width:200px;
    height:60px;
    position: absolute;
    left:50%;
    top:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 999999;
}
.circle{
    width:20px;
    height:20px;
    position: absolute;
    border-radius: 50%;
    background-color: black;
    left:15%;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    -webkit-animation: circle .5s alternate infinite ease;
            animation: circle .5s alternate infinite ease;
}

@-webkit-keyframes circle{
    0%{
        top:60px;
        height:5px;
        border-radius: 50px 50px 25px 25px;
        -webkit-transform: scaleX(1.7);
                transform: scaleX(1.7);
    }
    40%{
        height:20px;
        border-radius: 50%;
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
    100%{
        top:0%;
    }
}

@keyframes circle{
    0%{
        top:60px;
        height:5px;
        border-radius: 50px 50px 25px 25px;
        -webkit-transform: scaleX(1.7);
                transform: scaleX(1.7);
    }
    40%{
        height:20px;
        border-radius: 50%;
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
    100%{
        top:0%;
    }
}
.circle:nth-child(2){
    left:45%;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}
.circle:nth-child(3){
    left:auto;
    right:15%;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
.shadow{
    width:20px;
    height:4px;
    border-radius: 50%;
    background-color: rgba(0,0,0,.5);
    position: absolute;
    top:62px;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    z-index: -1;
    left:15%;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    -webkit-animation: shadow .5s alternate infinite ease;
            animation: shadow .5s alternate infinite ease;
}

@-webkit-keyframes shadow{
    0%{
        -webkit-transform: scaleX(1.5);
                transform: scaleX(1.5);
    }
    40%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        opacity: .7;
    }
    100%{
        -webkit-transform: scaleX(.2);
                transform: scaleX(.2);
        opacity: .4;
    }
}

@keyframes shadow{
    0%{
        -webkit-transform: scaleX(1.5);
                transform: scaleX(1.5);
    }
    40%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        opacity: .7;
    }
    100%{
        -webkit-transform: scaleX(.2);
                transform: scaleX(.2);
        opacity: .4;
    }
}
.shadow:nth-child(4){
    left: 45%;
    -webkit-animation-delay: .2s;
            animation-delay: .2s
}
.shadow:nth-child(5){
    left:auto;
    right:15%;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
